import { HttpClient } from '@angular/common/http';
import { Component } from '@angular/core';
import { Post } from '@nitro-assignment/api-interfaces';
import * as moment from 'moment';
import { from, Observable } from 'rxjs';
import { groupBy, mergeMap, reduce, toArray } from 'rxjs/operators';
import { environment } from '../environments/environment';

interface PostNode {
  name: string;
  children: Post[];
}

@Component({
  selector: 'nitro-assignment-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent {
  posts$ = this.http.get<Post[]>(`${environment.apiEndpoint}api/posts`);
  groupedPosts$: Observable<PostNode[]>;

  favoriteCategory: string = 'Week';
  postCategories: string[] = ['Week', 'Author', 'Location'];

  constructor(private http: HttpClient) {
    this.posts$.subscribe((posts) => {
       this.renderList('week');
    });
  }

  radioChange($event) {
    this.renderList($event.value.toLowerCase());
  }

  renderList(categoryType: string) {
    this.posts$.subscribe((posts) => {
      this.groupedPosts$ = from(posts).pipe(
        groupBy(post => post[categoryType], p => p) ,
        mergeMap(group => group
          .pipe(
            reduce((acc, cur) => {
                acc.children.push(cur);
                return acc;
              },{ 
                name: group.key, children: []
              }
            ))),toArray()
        );
    });
  }

  showPostDetails($event) {
    console.log($event);
  }
}
